
<template>
  <div class="categroy">
    <CateNavBar class="categroy_nav"></CateNavBar>

    <!-- <div class="categroy_main"> -->
    <div class="content">
      <CateNav :cateLists="cateLists" @cateNavClick="cateNavClick"></CateNav>
      <Scroll id="scroll">
        <CateNavContent :showCateContent="showCateContent"></CateNavContent>
        <TabControl :tabNums="tabNums" @tabcontrol="tabcontrol"></TabControl>
        <GoodsListShow :goods="showCateData" />
      </Scroll>
    </div>
    <!-- </div> -->
  </div>
</template>

<script>
import CateNavBar from "./childrenComps/CateNavBar";
import CateNav from "./childrenComps/CateNav";
import CateNavContent from "./childrenComps/CateNavContent";

import Scroll from "@/components/scroll/Scroll";
import TabControl from "@/components/tabControl/context/TabControl";
import GoodsListShow from "@/components/goodslist/GoodsListShow";

import {
  getCategory,
  getSubcategory,
  getCategoryDetail,
} from "@/network/category.js";

export default {
  name: "category",
  components: {
    CateNavBar,
    CateNav,
    Scroll,
    CateNavContent,
    TabControl,
    GoodsListShow,
  },
  data() {
    return {
      cateLists: [], //商品分类标题数据
      currentIndex: -1, //记录当前索引
      cateData: {}, //商品分类标题的数据
      tabNums: ["综合", "销量", "新品"],
      currentType: "pop",
    };
  },
  computed: {
    showCateContent() {
      if (this.currentIndex === -1) {
        return;
      }
      return this.cateData[this.currentIndex].RcateList;
    },
    showCateData() {
      if (this.currentIndex === -1) {
        return;
      }
      return this.cateData[this.currentIndex].cateDataDetail[this.currentType];
    },
  },
  created() {
    // getSubcategory("3627").then(res=>{
    //   console.log(res)
    // })
    //获取第一个分类标题的数据
    this.getCategoryData();
  },
  mounted() {},
  methods: {
    getCategoryData() {
      getCategory().then((res) => {
        console.log(res);
        /**获取商品分类标题数据 */
        this.cateLists = res.data.category.list;

        for (let i = 0; i < this.cateLists.length; i++) {
          // console.log(typeof(i))
          this.cateData[i] = {
            RcateList: {},
            cateDataDetail: {
              pop: [],
              new: [],
              sell: [],
            },
          };
        }
        console.log(this.cateData);

        /**获取第一个商品分类标题数据 */
        this.getSubcategoryes(0);
      });
    },
    getSubcategoryes(index) {
      //获取各个分类标题的数据
      this.currentIndex = index;
      const maitKey = this.cateLists[index].maitKey;
      console.log(maitKey);
      getSubcategory(maitKey).then((res) => {
        this.cateData[index].RcateList = res.data;
        // console.log(res);
        this.getCateDataDetail("pop");
        this.getCateDataDetail("new");
        this.getCateDataDetail("sell");
        this.cateData = { ...this.cateData };
      });
    },
    getCateDataDetail(type) {
      //获取各个分类的商品详情数据
      const miniWallkey = this.cateLists[this.currentIndex].miniWallkey;
      getCategoryDetail(miniWallkey, type).then((res) => {
        // console.log(res)
        this.cateData[this.currentIndex].cateDataDetail[type] = res;
      });
    },

    /**事件处理 */
    cateNavClick(index) {
      this.getSubcategoryes(index);
      // console.log(this.cateData[this.currentIndex].RcateList)
      console.log(this.cateData[this.currentIndex].cateDataDetail[this.currentType])
    },
    tabcontrol(index) {
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
    },
  },
};
</script>

<style scoped>
.categroy {
  height: 100vh;
}
.categroy_nav {
  background: pink;
  text-align: center;
  color: #fff;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 44px;
  bottom: 49px;

  display: flex;
}
#scroll {
  height: 100%;
  flex: 1;
  overflow: hidden;
}
</style>