<template>
  <Scroll id="cateNav">
    <div class="cate_nav">
      <div class="cate_nav_item" v-for="(item,index) in cateLists" :key="index" :class="{active: currentIndex === index}" @click="cateNavClick(index)">
        <p>{{item.title}}</p>
      </div>
    </div>
  </Scroll>
</template>

<script>
import Scroll from "@/components/scroll/Scroll";
export default {
  components: {
    Scroll,
  },
  props:{
    cateLists:{
      type: Array,
      default(){
        return []
      }
    }
  },
  data(){
    return{
      currentIndex: 0
    }
  },
  methods:{
    cateNavClick(index){
      this.currentIndex = index
      // console.log(index)
      this.$emit('cateNavClick',index)
    }
  }
};
</script>

<style scoped>
#cateNav {
  width: 100px;
  height: 100%;
  background: #f6f6f6;
  overflow: hidden;
}
.cate_nav_item {
  width: 100px;
  height: 45px;

  text-align: center;
  font-size: 14px;
  color: #666;
  box-sizing: border-box;
  position: relative;
}
.cate_nav_item p {
  position: absolute;
  width: 80%;
  height: 20px;
  top: 50%;
  transform: translateY(-50%);
}
.active {
  background: #fff;
  color: #ff5577;
}
.active p {
  border-left: 3px solid #ff5577;
  font-weight: 700;
}
</style>