<template>
  <div class="tab_control">
    <div
      class="tab_control_item"
      @click="tabControlClick(index)"
      v-for="(item, index) in tabNums"
      :key="index"
      :class="{ active: currentIndex === index }"
    >
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tabNums: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  methods: {
    tabControlClick(index) {
      this.currentIndex = index;
      this.$emit('tabcontrol',index)
    },
  },
};
</script>

<style scoped>
.tab_control {
  display: flex;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  background: #fff;
}
.tab_control_item {
  flex: 1;
  color: #666;
}
.tab_control_item span {
  padding: 5px;
}
.tab_control_item.active {
  color: #ff5777;
}
.tab_control_item.active span {
  border-bottom: 2px solid #ff5777;
}
</style>
