import request from './request'

/**
 * 获取商品分类标题
 */
export function getCategory() {
    return request({
        url: '/category'
    })
}

/**
 * 
 */
export function getSubcategory(maitKey) {
    return request({
        url: '/subcategory',
        params: {
            maitKey
        }
    })
}

export function getCategoryDetail(miniWallkey, type) {
    return request({
        url: '/subcategory/detail',
        params: {
            miniWallkey,
            type
        }
    })
}